import React from 'react'

const addApplicationForm = [
  [{ render: () => <h2 className="mb-2 mt-2">Run an Application</h2> }],
  [{ name: 'applicantName', type: 'text', label: 'Applicant Name:', placeholder: 'John Appleseed' }],
  [{ name: 'email', type: 'email', label: 'Email:', placeholder: 'johnappleseed@apple.com' }],
  [{ render: ({ handleSubmit, isSubmitting, touched, errors }) => (
    <div>
      <button
        className="btn btn-primary col-md-12 mt-3"
        onClick={handleSubmit}
        children="Run"
        disabled={isSubmitting}
      />
      {touched && errors.form && (
        <p className="text-danger">{errors.form}</p>
      )}
    </div>
  )}],
]

export default addApplicationForm


