import React from 'react'

import Appbar from '../Components/AppBar'
import Header from '../Components/Header'
import ReasonList from '../Components/ReasonList'
import Credits from '../Components/Credits'
import VideoHolder from '../Components/VideoHolder'
import Footer from '../Components/Footer'

const REASONS_1 = [
  ["https://www.swopsmart.com/assets/front/images/how-22.png", "Free"],
  ["https://daks2k3a4ib2z.cloudfront.net/573d5a1eee15ab7f704a5af7/57e20e6026805c3105f181f4_Icon-user-experience.png", "Intuitive"],
  ["http://dynamicworklabs.com/wp-content/uploads/2016/07/fast-archive-migration-icon.gif", "Quick and Easy"],
  ["https://png.icons8.com/agent/color/1600", "Tailored Results"],
]

const REASONS_2 = [
  ["http://www.azfraud.com/uploads/1/0/3/0/103063182/background-check-icon_1_orig.png", "Background Check"],
  ["https://d30y9cdsu7xlg0.cloudfront.net/png/89556-200.png", "Credit Report"],
  ["https://d30y9cdsu7xlg0.cloudfront.net/png/497181-200.png", "Pay Stubs"],
  ["https://www.iconexperience.com/_img/i_collection_png/512x512/plain/id_card.png", "Photo ID"],
]

const App = ({
  history
}) => (
  <div className="bg-white">
    <Appbar />
    <Header />
    <ReasonList
      title="Reasons Why You'll Love Us"
      reasons={REASONS_1}
      rounded={true}
    />
    <VideoHolder />
    <ReasonList
      title="Your Report Will Include"
      reasons={REASONS_2}
    />
    <Credits
      history={history}
    />
    <Footer />
  </div>
)

export default App


