import React from 'react'
import { NavLink, withRouter } from 'react-router-dom'
import Logo from '../Components/images/inlineLogo.png'

const AppBar = ({
  history,
  user = {},
}) => (
  <nav className="d-flex align-items-center nav nav-pills pl-2 pr-2 pb-3 border bg-white">
    <img
      alt=""
      className="d-none d-sm-inline-flex cursor-pointer"
      onClick={() => history.push('/')}
      style={{ marginRight: 20, height: 80 }}
      src={Logo}
    />
    <div className="d-flex justify-content-between f-1">
      <div className="d-flex flex-row pt-3">
        <NavLink
          exact
          className="nav-link"
          to="/application1"
          children="Request A Demo"
        />
      </div>
      <div className="d-flex flex-row pt-3">
        {localStorage.getItem('auth') ? ([
          <NavLink
            key="1"
            className="nav-link mr-2"
            to="/dashboard"
            children="Dashboard"
            exact
          />,
          <NavLink
            key="2"
            className="nav-link bg-danger text-white"
            to="/login"
            onClick={() => localStorage.removeItem('auth')}
            children="Logout"
          />
        ]) : ([
          <NavLink
            key="1"
            className="nav-link mr-2"
            to="/login"
            children="Login"
            exact
          />,
          <NavLink
            key="2"
            className="nav-link"
            to="/signup"
            children="Signup"
            exact
          />
        ])}
      </div>
    </div>
  </nav>
)

export default withRouter(AppBar)


